.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar {
  composes: center;
  width: 100%;
  height: 4rem;
  background-color: #4285f4;
  box-shadow: 00.2rem 0.2rem 0.5rem #555;
  position: absolute;
  z-index: 300;
}

.sticky {
  position: fixed;
  top: 0;
}

